.banner {
  
    background-size: cover;
    height: 447px;
    color: white;
}

.content {
    padding-top: 140px;
    height: 190px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom:0.3rem;
}

.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.4rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,.5);
    cursor: pointer;
    margin: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 519px;
}

.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}