.row {
    margin-left: 20px;
    color: #fff;
}

.posters {
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.posters::-webkit-scrollbar {
    display: none;
}

.poster {
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;
}

.smallposter {
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}

.poster:hover {
    transform: scale(1.3);
}

.smallposter:hover{
    transform: scale(1.3);
}
.movie {
    color: #ff4fe2b0;
    padding-left: 863px;
    margin: 27px;
}